
html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}
body {
  margin: 0;
}
:root {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  font-family: sans-serif;
  margin: 0 0;
  color: white;
  background-color: #EDEDED;
}
#App {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  height: 100vh;
}
.presentation {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 25%;
          flex: 1 1 25%;
  -ms-flex-preferred-size: 200px;
      flex-basis: 200px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
  background-color: #13507D;
}
.presentation > * {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 100%;
          flex: 1 1 100%;
}
.presentation__img {
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.presentation__img figure {
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.presentation__img img {
  border-radius: 50%;
}
.presentation__info {
  -webkit-box-flex: 5;
      -ms-flex-positive: 5;
          flex-grow: 5;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  text-align: center;
  background-color: #C4C4C4;
  margin: 10px 30px;
  border-radius: 5%;
}
.presentation__info_text {
  background-color: #EDE6E6;
  margin-top: 15px;
  padding: 0 10px;
  border-radius: 3%;
}

/* nav */
.menu__list {
  padding: 0;
  list-style: none;
}
.menu__list li {
  margin-top: 5px;
}
.menu__list li a {
  text-decoration: none;
  color: white;
  padding: 3px 2px;
}
.menu__list li a:hover {
  background-color: #635858;
}

/* /nav */

/* social */
.presentation__info_social {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

/* /social */

/* layout */
.layout__title {
  background-color: #A7C4D9;
  margin: 0 15px;
  border-radius: 1%;
}
.layout__title h2 {
  padding: 2px 2px 0 2px;
}
.layout {
  -webkit-box-flex: 4;
      -ms-flex: 4 1 75%;
          flex: 4 1 75%;
  -ms-flex-preferred-size: 400px;
      flex-basis: 400px;
  height: 100vh;
  overflow-y: scroll;
  overflow-x: hidden;
}
.layout__works {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.layout__works_item {
  margin-bottom: 15px;
}
.layout__works_item  figure {
  margin: 0 35px;
}
.layout_works_item_description {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: #C4C4C4;
  margin: 0 35px;
  border-radius: 3px;
}
.layout_works_item_description_aside {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  text-align: center;
}
.layout_works_item_description_aside a {
  color: white;
  text-decoration: none;
}

/* certificados */
.layout__certs {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
}
.layout__certs_item {
  background-color:  #C4C4C4;
  /* margin-bottom: 15px; */
  margin: 5px 22px;
  border-radius: 2%;
}
.layout__certs_item figure {
  margin: 10px 10px 10px 10px;
  /* padding: 5px 5px;  */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}

/* /certificados */

/* conocimientos */
.layout__knowledge {
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  text-align: center;
  -webkit-box-align: stretch;
      -ms-flex-align: stretch;
          align-items: stretch;
}
.layout__knowledge_col {
  margin: 20px;
  padding: 20px;
  /* text-align: center; */
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  background-color: #C4C4C4;
  text-align: center;
  border-radius: 5%;
}
.layout__knowledge_col > * {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 100%;
          flex: 1 1 100%;
}
.layout__knowledge_col_features {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 0;
  margin-top: 0;
}
.layout__knowledge_col_features li {
  text-align: inherit;
  margin-top: 5px;
  list-style: none;
}
.layout__knowledge_col_features:last-child {
  border-bottom: 0;
}
.layout__knowledge_col > * {
  -webkit-box-flex: 1;
      -ms-flex: 1 1 100%;
          flex: 1 1 100%;
}

/* /conocimientos */

/* Info y contacto */
.layout__info {
  display: grid;
  grid-gap: 15px;
  margin: 0 15px;
  grid-template-columns: repeat(2, 1fr)
}
.layout__info__bio {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display:-webkit-box;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  -ms-flex-line-pack: center;
      align-content: center;
  border-radius: 1%;
  background: #DB8F8F;
}
.layout__info__bio h4,
.layout__info__bio p {
  margin: 5px 15px;
}
.layout__info__bio_studies {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
.layout__info__contact {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background: #DB8F8F;
  border-radius: 1%;
}
.layout__info__contact h4 {
  margin: 5px 0;
}

/* /Info y contacto */

/* /layout */


